            <v-card>
              <v-card-title class="text-h6">
                <p>Você está prestes a alterar os dados do veículo. </p>
              </v-card-title>
              <v-card-text>
                <p>
                  Tem certeza que a alteração desses dados não causará nenhum
                  problema futuro?
                </p>
              </v-card-text>
              <v-card-actions>
                <v-spacer />

                <v-btn
                  color="red darken-2"
                  text
                  autofocus
                  @click="closeVehicleDialogSave"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-3"
                  text
                  @click="editVehicleConfirm"
                >
                  Sim
                </v-btn>

                <v-spacer />
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog
            v-model="vehicleDialogDelete"
            max-width="500px"
          >
            <v-card>
              <v-card-title class="text-h5">
                Tem certeza que excluir esse lançamento não causará nenhum
                problema futuro?
              </v-card-title>

              <v-card-actions>
                <v-spacer />

                <v-btn
                  color="red darken-2"
                  text
                  autofocus
                  @click="vehicleCloseDelete"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-3"
                  text
                  @click="deleteItemConfirm"
                >
                  Sim
                </v-btn>

                <v-spacer />
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          id="editBtn"
          small
          class="mr-2"
          @click="editVehicle(item), disabledField(), (showVehicleCloneBtn = true), maskCheckbox(), setEditTime()"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          id="addBtn"
          class="mr-2"
          @click="addLicence(item), greaterLicenceId(), getVehicleID(item), editedLicence.STATUS = 'PRE-PEDIDO'"
        >
          mdi-text-box-plus-outline
        </v-icon>
        <v-icon
          id="newLicenceBtn"
          v-show="false"
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>

      <template v-slot:no-data>
        <v-btn
          color="blue"
          dark
          @click="fetchFleet"
        >
          Carregar Lista
        </v-btn>
      </template>

      <template v-slot:expanded-item="{ headers, item }">
        <td
          :colspan="headers.length"
          class="pa-2 ma-2"
        > 
          <v-simple-table max-width="80%">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Código
                  </th>
                  <th class="text-left">
                    Tipo
                  </th>
                  <th class="text-left">
                    AET
                  </th>
                  <th class="text-left">
                    Emissão
                  </th>
                  <th class="text-left">
                    Vencimento
                  </th>
                  <th class="text-left">
                    Visualizar
                  </th>
                </tr>
              </thead>
              <tbody>
                <v-progress-linear
                  v-if="licencesLoader"
                  indeterminate
                  color="blue darken-2"
                  absolute
                  style="margin-left: 2%; margin-right: 2%; width: 96%"
                />
                <tr
                  v-for="licence in licences"
                  :key="licence.ID"
                >
                  <td v-if="licence.VEICULOID === item.ID">{{ licence.ID }}</td>
                  <td v-if="licence.VEICULOID === item.ID">{{ licence.TIPO }}</td>
                  <td v-if="licence.VEICULOID === item.ID">{{ licence.AET }}</td>
                  <td v-if="licence.VEICULOID === item.ID">{{ licence.EMISSAO }}</td>
                  <td v-if="licence.VEICULOID === item.ID">{{ licence.VENCIMENTO }}</td>
                  <td v-if="licence.VEICULOID === item.ID">
                    <v-icon
                      id="watchBtn"
                      small
                      class="mr-2"
                      @click="watchLicence(licence)"
                    >
                      mdi-eye
                    </v-icon>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

