<script>
  import axios from 'axios'
  import Vue from 'vue'

  export default {
    name: 'FleetControl',

    filters: {
      currencyBRL (valor) {
        return valor.toLocaleString('pt-BR', {
          style: 'currency',
          currency: 'BRL',
        })
      },
    },

    data: () => ({
      vId: null,
      lId: null,
      repeatedV: false,
      repeatedL: false,
      valid: true,
      checkboxAtivo: false,
      checkboxNF: false,
      vehicleDialog: false,
      vehicleDialogSave: false,
      vehicleDialogDelete: false,
      licenceRegisterDialog: false,
      licenceWatchDialog: false,
      licenceDialogSave: false,
      licenceDialogDelete: false,
      licencesLoader: true,
      showUpdate: false,
      showPost: false,
      showError: false,
      showVehicleCloneBtn: true,
      isDisabled: false,
      logado: window.localStorage.logado,
      user: window.localStorage.user,
      razaoSocial: window.localStorage.razaosocial,
      plateMask: 'AAA-#X##',
      search: '',
      form: '',
      now: '',
      timeNow: '',
      err: '',
      weight: '',
      length: '',
      width: '',
      height: '',
      tempUSUARIO: '',
      tempTARA: '',
      tempPLACA: '',
      tempATIVO: '',
      tempMARCA: '',
      tempMODELO: '',
      tempCONJUNTOPRINCIPAL: '',
      tempANOFAB: '',
      tempCHASSI: '',
      tempEIXO: '',
      tempCARROCERIA: '',
      tempPOTENCIA: '',
      tempRENAVAM: '',
      tempCMT: '',
      tempRNTRC: '',
      type: '',
      subtype: '',
      originState: '',
      destinationState: '',
      originCity: '',
      destinationCity: '',
      states: [],
      originCities: [],
      destinationCities: [],
      expanded: [],
      fleet: [],
      licences: [],
      // licences: [],
      brands: [
        { value: 'DAF', text: 'DAF' },
        { value: 'Iveco', text: 'Iveco' },
        { value: 'MAN', text: 'MAN' },
        { value: 'Mercedes', text: 'Mercedes' },
        { value: 'Scania', text: 'Scania' },
        { value: 'Volkswagen', text: 'Volkswagen' },
        { value: 'Volvo', text: 'Volvo' },
      ],
      statuses: [
        // { value: 'CONCLUIDA', text: 'Concluída', disabled: true },
      ],
      headers: [
        { text: 'ID', value: 'ID', align: ' d-none' }, // it is important to leave a space before the d-none string
        { text: 'Marca', value: 'MARCA' },
        { text: 'Placa', value: 'PLACA' },
        { text: 'Ativo', value: 'ATIVO' },
        { text: 'Edição', value: 'actions', sortable: false },
      ],
      editedVIndex: -1,
      editedLIndex: -1,
      editedVehicle: {
        ID: '',
        CADASTRO: '',
        USUARIO: '',
        PLACA: '',
        ANOFAB: '',
        CHASSI: '',
        MARCA: '',
        MODELO: '',
        CARROCERIA: '',
        POTENCIA: '',
        CMT: '',
        TARA: '',
        RENAVAM: '',
        RNTRC: '',
        CONJUNTOPRINCIPAL: '',
        ATIVO: 'N',
        EDICAO: '',
      },
      editedLicence: {
        ID: '',
        VEICULOID: '',
        STATUS: 'PRE-PEDIDO',
        PLACA: '',
        AET: '',
        EMISSAO: '',
        VENCIMENTO: '',
        TIPO: '',
        TRACAO: '',
        CONJUNTO: '',
        ALTURA: '',
        LARGURA: '',
        COMPRIMENTO: '',
        CARGA: '',
        UFORIGEM: '',
        UFDESTINO: '',
        CIDADEORIGEM: '',
        CIDADEDESTINO: '',
        UNTRACIONADAS: '',
      },
      defaultVehicle: {
        ID: '',
        CADASTRO: '',
        USUARIO: '',
        PLACA: '',
        ANOFAB: '',
        CHASSI: '',
        MARCA: '',
        MODELO: '',
        CARROCERIA: '',
        POTENCIA: '',
        CMT: '',
        TARA: '',
        RENAVAM: '',
        RNTRC: '',
        CONJUNTOPRINCIPAL: '',
        ATIVO: 'N',
        EDICAO: '',
      },
      defaultLicence: {
        ID: '',
        VEICULOID: '',
        STATUS: 'PRE-PEDIDO',
        PLACA: '',
        AET: '',
        EMISSAO: '',
        VENCIMENTO: '',
        TIPO: '',
        TRACAO: '',
        CONJUNTO: '',
        ALTURA: '',
        LARGURA: '',
        COMPRIMENTO: '',
        CARGA: '',
        UFORIGEM: '',
        UFDESTINO: '',
        CIDADEORIGEM: '',
        CIDADEDESTINO: '',
        UNTRACIONADAS: '',
      },
      truckTypes: [
        {
          id: 1,
          name: "CVC - Combinação Veicular de Carga",
          subtypes: ['Rodotrem 9 eixos', 'Bitrem 9 eixos', 'Romeu e Julieta', 'Bitrem 7 eixos']
        },
        {
          id: 2,
          name: "Prancha",
          subtypes: ['Vazio', 'Carga Indivisível', 'Superdimensionada e Indivisível']
        },
        {
          id: 3,
          name: "Baú",
          subtypes: ['Vazio ou Carregado']
        },
        {
          id: 4,
          name: "Guindaste",
          subtypes: ['Até 48 toneladas', 'Acima de 48 toneladas']
        },
      ],
    }),

    computed: {
      vehicleFormTitle () {
        return this.editedVIndex === -1
          ? 'Novo Veículo'
          : `Editar Veículo - ID ${this.editedVehicle.ID}`
      },
      licenceFormTitle () {
        return this.editedLIndex === -1
          ? 'Nova Licença'
          : `Licença - ${this.editedLicence.AET} - ${this.editedLicence.CONJUNTO}`
      },

      rulesANOFAB () {
        const rules = []

        const rule = (this.editedVehicle.ANOFAB >= 2000 || 'Veículo muito antigo para rodar')
        rules.push(rule)

        return rules
      },
    },

    watch: {
      vehicleDialog (val) {
        val || this.vehicleClose()
        if (this.editedVehicle.PLACA.includes('NF-')) {
          return ((this.checkboxNF = true), (this.plateMask = '?NF-#################'))
        }
        if (this.editedVehicle.ATIVO === "S") {
          return this.checkboxAtivo = true
        } 
        if (this.editedVehicle.ATIVO === "N") {
          return this.checkboxAtivo = false
        }
      },
      licenceDialog (val) {
        val || this.licenceClose()
      },

      vehicleDialogSave (val) {
        val || this.closeVehicleDialogSave()
      },
      licenceDialogSave (val) {
        val || this.closeLicenceDialogSave()
      },

      vehicleDialogDelete (val) {
        val || this.vehicleCloseDelete()
      },
      licenceDialogDelete (val) {
        val || this.licenceCloseDelete()
      },
      checkboxAtivo () {
        this.activeVehicle()
      },
      err () {
        console.log(this.err)
        alert('Um erro foi encontrado. Por favor, entre em contato com o suporte.')
      },
      expanded () {
        this.fetchLicences()
      },
      licences () {
        this.licencesLoader = false
      }
    },

    created () {
      this.fetchFleet()
      this.maskCheckbox()
      Vue.config.warnHandler = (err => this.err = err)
    },

    methods: {
      validate () {
        this.$refs.observer.validate()
      },
      maskCheckbox () {
        if (this.checkboxNF === true) {
          this.plateMask = 'NF-##############'
        } else {
          this.plateMask = 'AAA-#X##'
        }
      },
      activeVehicle () {
        if (this.checkboxAtivo === true) {
          this.editedVehicle.ATIVO = 'S'
        } else {
          this.editedVehicle.ATIVO = 'N'
        }
      },
      disabledField () {
        if (this.editedVIndex > -1) {
          this.isDisabled = true
        } else if (this.editedVIndex === -1) {
          this.isDisabled = false
        }
      },

      setTimeNow () {
        var date = new Date()
        var day = date.getDate()
        var month = date.getMonth() + 1
        var year = date.getFullYear()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        this.now = day + '/' + month + '/' + year

        if (this.editedVIndex === -1) {
          this.editedVehicle.CADASTRO = this.now
        }
      },
      setEditTime () {
        const timeElapsed = Date.now()
        const rightNow = new Date(timeElapsed)
        this.timeNow = rightNow.toLocaleString('pt-BR')
      },

      setUser () {
        if (this.editedVIndex === -1) {
          this.editedVehicle.USUARIO = this.user
        }
      },

      formatDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [year, month, day] = tempDate.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [day, month, year] = tempDate.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },

      // getColor (STATUS) {
      //   if (STATUS === 'PAGO') {
      //     return 'blue lighten-4'
      //   } else if (STATUS === 'A VENCER') {
      //     return 'green lighten-4'
      //   } else if (STATUS === 'TAXA PAGA') {
      //     return 'purple lighten-4'
      //   } else if (STATUS === 'A COBRAR') {
      //     return 'red lighten-4'
      //   } else if (STATUS === 'CONFERIR') {
      //     return 'orange lighten-4'
      //   } else if (STATUS === 'VENCIDO') {
      //     return 'red lighten-4'
      //   } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
      //     return 'red lighten-4'
      //   } else if (STATUS === '') {
      //     return ''
      //   } else { return 'grey lighten-2' }
      // },

      // getIcon (STATUS) {
      //   if (STATUS === 'PAGO') {
      //     return 'mdi-check-all blue--text'
      //   } else if (STATUS === 'A VENCER' || STATUS === 'A DEPOSITAR') {
      //     return 'mdi-cash-clock'
      //   } else if (STATUS === 'TAXA PAGA') {
      //     return 'mdi-check'
      //   } else if (STATUS === 'A COBRAR') {
      //     return 'mdi-clock-alert-outline'
      //   } else if (STATUS === 'CONFERIR') {
      //     return 'mdi-email-alert-outline'
      //   } else if (STATUS === 'VENCIDO') {
      //     return 'mdi-alert red--text'
      //   } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
      //     return 'mdi-cash-remove red--text'
      //   } else { return 'mdi-list-status' }
      // },

      fetchFleet () {
        return fetch('https://sheetdb.io/api/v1/gy59g6tp4jo4p')
          .then((r) => r.json())
          .then((r) => {
            this.fleet = r
          })
      },
      fetchLicences () {
        return fetch('https://sheetdb.io/api/v1/1q4dfgpu4na0b')
          .then((res) => res.json())
          .then((res) => {
            this.licences = res
          })
      },

      greaterVehicleId () {
        return axios.get('https://sheetdb.io/api/v1/gy59g6tp4jo4p').then((r) => {
          this.vId = r.data.length + 1
          for (let i = 0; i < r.data.length; i++) {
            Number(this.vId) === Number(r.data[i].ID) ? this.repeatedV = true : this.repeatedV = false
          }
          if (this.editedVIndex === -1) {
            this.repeatedV === true ? alert('ID duplicado. Favor contatar suporte e suspender novos lançamentos até que a correção seja confirmada.') : this.editedVehicle.ID = this.vId
          }
        })
      },
      greaterLicenceId () {
        return axios.get('https://sheetdb.io/api/v1/1q4dfgpu4na0b').then((res) => {
          this.lId = res.data.length + 148000
          for (let j = 0; j < res.data.length; j++) {
            Number(this.id) === Number(res.data[j].ID) ? this.repeatedL = true : this.repeatedL = false
          }
          if (this.editedLIndex === -1) {
            this.repeatedL === true ? alert('ID duplicado. Favor contatar suporte e suspender novos lançamentos até que a correção seja confirmada.') : this.editedLicence.ID = this.lId
          }
        })
      },
      getVehicleID (item) {
        this.editedLicence.VEICULOID = item.ID
      },
      addLicence (item) {
        this.editedLIndex = this.licences.indexOf(item)
        this.editedLicence = Object.assign({}, item)
        this.licenceRegisterDialog = true
      },
      watchLicence (val) {
        this.editedLIndex = this.licences.indexOf(val)
        this.editedLicence = Object.assign({}, val)
        this.licenceWatchDialog = true
        console.log(val)
      },
      editVehicle (item) {
        this.err.includes('Duplicado')
        ? (alert('Não atualizar lançamentos e contatar suporte.'), this.vehicleClose())
        : (this.editedVIndex = this.fleet.indexOf(item), this.editedVehicle = Object.assign({}, item), this.vehicleDialog = true, console.log(item))
      },
      deleteItem (item) {
        this.editedVIndex = this.fleet.indexOf(item)
        this.editedVehicle = Object.assign({}, item)
        this.vehicleDialogDelete = true
      },

      editVehicleConfirm () {
        // salva edição
        if (this.editedVIndex > -1) {
          axios.put(`https://sheetdb.io/api/v1/gy59g6tp4jo4p/ID/${this.editedVehicle.ID}`, {
            data: {
              USUARIO: `${this.editedVehicle.USUARIO}`,
              TARA: `${this.editedVehicle.TARA}`,
              PLACA: `${this.editedVehicle.PLACA}`,
              ATIVO: `${this.editedVehicle.ATIVO}`,
              MARCA: `${this.editedVehicle.MARCA}`,
              MODELO: `${this.editedVehicle.MODELO}`,
              CONJUNTOPRINCIPAL: `${this.editedVehicle.CONJUNTOPRINCIPAL}`,
              ANOFAB: `${this.editedVehicle.ANOFAB}`,
              CHASSI: `${this.editedVehicle.CHASSI}`,
              CARROCERIA: `${this.editedVehicle.CARROCERIA}`,
              POTENCIA: `${this.editedVehicle.POTENCIA}`,
              CMT: `${this.editedVehicle.CMT}`,
              RENAVAM: `${this.editedVehicle.RENAVAM}`,
              RNTRC: `${this.editedVehicle.RNTRC}`,
              EDICAO: `${this.editedVehicle.EDICAO}` + ` ${this.user} ${this.timeNow}.`,
            },
          })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showUpdate = !this.showUpdate
                this.fetchFleet()
                setTimeout(() => {
                  this.showUpdate = !this.showUpdate
                }, 1500)
              } else {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.vehicleDialogSave = false
          setTimeout(() => {
            this.vehicleClose()
          }, 200)
        } else {
          this.vehicleSave()
        }
      },
      deleteItemConfirm () {
        this.fleet.splice(this.editedVIndex, 1)
        this.vehicleCloseDelete()
      },

      vehicleClose () {
        this.checkboxNF = false
        this.editedVehicle.ATIVO = 'N'
        this.vehicleDialog = false
        this.isDisabled = false
        this.$nextTick(() => {
          this.editedVehicle = Object.assign({}, this.defaultVehicle)
          this.editedVIndex = -1
        })
      },
      licenceClose () {
        this.licenceRegisterDialog = false
        this.licenceWatchDialog = false
        this.$nextTick(() => {
          this.editedLicence = Object.assign({}, this.defaultLicence)
          this.editedLIndex = -1
        })
      },

      closeVehicleDialogSave () {
        this.vehicleDialogSave = false
      },
      vehicleCloseDelete () {
        this.vehicleDialogDelete = false
        this.$nextTick(() => {
          this.editedVehicle = Object.assign({}, this.defaultVehicle)
          this.editedVIndex = -1
        })
      },

      validateField () {
        this.$refs.form.validate()
      },

      vehicleClone () {
        if (this.editedVIndex > -1) {
          this.tempUSUARIO = this.editedVehicle.USUARIO,
          this.tempTARA = this.editedVehicle.TARA,
          this.tempPLACA = this.editedVehicle.PLACA,
          this.tempATIVO = this.editedVehicle.ATIVO,
          this.tempMARCA = this.editedVehicle.MARCA,
          this.tempMODELO = this.editedVehicle.MODELO,
          this.tempCONJUNTOPRINCIPAL = this.editedVehicle.CONJUNTOPRINCIPAL,
          this.tempANOFAB = this.editedVehicle.ANOFAB,
          this.tempCHASSI = this.editedVehicle.CHASSI,
          this.tempPOTENCIA = this.editedVehicle.POTENCIA,
          this.tempCARROCERIA = this.editedVehicle.CARROCERIA,
          // 3.fechar o popup OK
          setTimeout(() => {
            this.vehicleClose()
          }, 300)
          // 4.abrir popup de novo pedido
          setTimeout(() => {
            document.getElementById('newVehicleBtn').click()
          }, 300)
          // 5.popular os campos com as informações salvas anteriormente
          setTimeout(() => {
            this.editedVehicle.USUARIO = this.tempUSUARIO,
            this.editedVehicle.TARA = this.tempTARA,
            this.editedVehicle.PLACA = this.tempPLACA,
            this.editedVehicle.ATIVO = this.tempATIVO,
            this.editedVehicle.MARCA = this.tempMARCA,
            this.editedVehicle.CONJUNTOPRINCIPAL = this.tempCONJUNTOPRINCIPAL,
            this.editedVehicle.ANOFAB = this.tempANOFAB,
            this.editedVehicle.CHASSI = this.tempCHASSI,
            this.editedVehicle.POTENCIA = this.tempPOTENCIA,
            this.editedVehicle.CARROCERIA = this.tempCARROCERIA
            this.editedVehicle.MODELO = this.tempMODELO
          }, 300)
        } else {
          this.showVehicleCloneBtn = false
        }
      },
      licenceClone () {
        if (this.editedLIndex > -1) {
          // this.tempCOMPRIMENTO = this.editedVehicle.COMPRIMENTO,
          setTimeout(() => {
            this.licenceClose()
          }, 300)
          setTimeout(() => {
            document.getElementById('newLicenceBtn').click()
          }, 300)
          setTimeout(() => {
            // this.editedVehicle.COMPRIMENTO = this.tempCOMPRIMENTO
          }, 300)
        } else {
          this.showLicenceCloneBtn = false
        }
      },

      vehicleSave () {
        var form = document.getElementById('sheetdb-form-v')
        if (this.editedVIndex > -1) {
          this.vehicleDialogSave = true
        } else {
          // salva novo
          if (!this.vId) {
            alert('lançamento sem ID. Favor contatar suporte antes de prosseguir.')
          }
          fetch(form.action, {
            method: 'POST',
            body: new FormData(form),
          }).then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showPost = !this.showPost
              this.fetchFleet()
              setTimeout(() => {
                this.showPost = !this.showPost
              }, 1500)
            } else if (response.status === 429) {
              alert('O limite de solicitações foi excedido! Entre em contato com o suporte para liberar mais espaço.')
              window.location.reload()
            } else if (response.status === 401 || response.status === 403) {
              alert('Erro de servidor. Entre em contato com o suporte.')
              window.location.reload()
            }
          })
          this.vehicleClose()
        }
      },
      licenceSave () {
        var form = document.getElementById('sheetdb-form-l')
        if (this.editedLIndex > -1) {
          this.licenceDialogSave = true
        } else {
          // salva novo
          fetch(form.action, {
            method: 'POST',
            body: new FormData(form),
          }).then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showPost = !this.showPost
              this.fetchLicences()
              setTimeout(() => {
                this.showPost = !this.showPost
              }, 1500)
            } else if (response.status === 429) {
              alert('O limite de solicitações foi excedido! Entre em contato com o suporte para liberar mais espaço.')
              window.location.reload()
            } else if (response.status === 401 || response.status === 403) {
              alert('Erro de servidor. Entre em contato com o suporte.')
              window.location.reload()
            }
          })
          this.licenceClose()
        }
      },
      getStates() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados?orderBy=nome`)
        .then(r => r.json())
        .then((data) => {
          this.states = data
        })
      },
      getCitiesOrigin() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.originState}/municipios`)
        .then(r => r.json())
        .then((data) => {
          this.originCities = data
        })
      },
      getCitiesDestination() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.destinationState}/municipios`)
        .then(r => r.json())
        .then((data) => {
          this.destinationCities = data
        })
      },
    },
  }
</script>

